@import '../styles/_global.scss';
.new-post {
  margin: 0 175px 10px 202px;
  padding-top: 43px;
  font-family: $sans-serif;
  background: $light;
  z-index: 7;
}
.new-post__form {
  padding-bottom: 150px;
}
.new-post__actions {
  float: right;
}
.new-post__save,
.new-post__cancel {
  font-weight: 700;
  font-size: 20px;
  color: $dark;
  letter-spacing: 2px;
  padding-left: 42px;
  padding-right: 6px;
}
.new-post__timestamp {
  padding-top: 57px;
  letter-spacing: 4px;
  font-size: 1.24em;
}
.new-post__title-container,
.new-post__content-container {
  position: relative;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: flex-end;
  margin-right: 7px;
}
.new-post__title {
  font-family: $yg-bold;
  font-size: 40px;
  resize: none;
  width: 96.7%;
  margin-top: -1px;
  letter-spacing: 4px;
  padding: 11px 15px 7px 19px;
}
.new-post__content {
  font-family: $yg-medium;
  font-size: 18px;
  resize: none;
  margin-top: -1px;
  letter-spacing: 2px;
  padding: 18px 11px 26px 23px;
  width: 96.7%;
}
.new-post__file-upload {
  display:flex;
  flex-flow: column nowrap;
  align-items:center;
  justify-content:center;
  margin: 31px 7px 37px 0;
}
.new-post__file-upload-container {
  width: 100%;
}
.file-preview__container{
  background: #D6D6D6;
  height: 540px;
  display: flex;
  position: relative;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}
#file-preview {
  height: 540px;
  display:none;
}
.upload-image {
  position: absolute;
  left: 0;
  bottom: 23px;
}
.upload-image__input {
  display:none;
}
.upload-image__label {
  font-family: $sans-serif;
  font-weight: 700;
  font-size: 20px;
  letter-spacing: 2px;
  text-transform: Uppercase;
  position: relative;
  width: 19.1%;
  padding: 23px 32px 23px 38px;
  text-align: center;
  background: $dark;
  color: $light;
  cursor: pointer;
}